import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { Note } from "../models";
import { addNote, editNote, fetchedNotes, removeNote } from "./note-slice";

const URL = "https://my-project-83d90-default-rtdb.firebaseio.com/notes.json";

//    
export const fetchNotes = createAsyncThunk(
  "notes/fetchNotes",
  async function (_, { rejectWithValue, dispatch }) {
    try {
      const response = await fetch(URL);

      const data = await response.json();
      dispatch(fetchedNotes(data));
    } catch (error) {
      return rejectWithValue(error);
    }
  }
);

//    
export const deleteNote = createAsyncThunk(
  "notes/deleteNote",
  async function (id: string, { rejectWithValue, dispatch }) {
    try {
      // -  Firebase   ,  id    id ,    
      const response = await fetch(URL);
      const data = await response.json();
      let serverId = "";

      for (const key in data) {
        if (data[key].id === id) {
          serverId = key;
        }
      }

      //      id       
      const responseDelete = await fetch(
        `https://my-project-83d90-default-rtdb.firebaseio.com/notes/${serverId}.json`,
        { method: "DELETE" }
      );

      if (!responseDelete.ok) {
        throw new Error("   ");
      }

      dispatch(removeNote(id));
    } catch (error: any) {
      return rejectWithValue(error.message);
    }
  }
);

//    
export const editNoteServer = createAsyncThunk(
  "notes/edinNoteServer",
  async function (note: Note, { rejectWithValue, dispatch }) {
    try {
      // -  Firebase   ,  id    id ,    
      const response = await fetch(URL);
      const data = await response.json();
      let serverId = "";

      for (const key in data) {
        if (data[key].id === note.id) {
          serverId = key;
        }
      }

      const editedNnote = {
        id: note.id,
        text: note.text,
        tags: note.tags,
        time: new Date(),
      };

      //      id       
      const responseDelete = await fetch(
        `https://my-project-83d90-default-rtdb.firebaseio.com/notes/${serverId}.json`,
        {
          method: "PATCH",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(editedNnote),
        }
      );

      if (!responseDelete.ok) {
        throw new Error("   ");
      }

      dispatch(editNote(note));
    } catch (error: any) {
      return rejectWithValue(error.message);
    }
  }
);

//     
export const addNewNote = createAsyncThunk(
  "notes/addNewNote",
  async function (note: Note, { rejectWithValue, dispatch }) {
    try {
      const newNnote = {
        id: note.id,
        text: note.text,
        tags: note.tags,
        time: new Date(),
      };
      const response = await fetch(URL, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(newNnote),
      });

      if (!response.ok) {
        throw new Error("   ");
      }

      dispatch(
        addNote({ id: newNnote.id, text: newNnote.text, tags: newNnote.tags })
      );
    } catch (error: any) {
      return rejectWithValue(error.message);
    }
  }
);

interface Istatus {
  status: string;
  satatusMsg: string;
}
const initialState: Istatus = {
  status: "",
  satatusMsg: "",
};
export const statusSlice = createSlice({
  name: "statusState",
  initialState,
  reducers: {},
  extraReducers(builder) {
    builder.addCase(fetchNotes.pending, (state) => {
      state.status = "loading";
      state.satatusMsg = " ...";
    });
    builder.addCase(fetchNotes.rejected, (state) => {
      state.status = "rejected";
      state.satatusMsg = "   ...";
    });
    builder.addCase(fetchNotes.fulfilled, (state) => {
      state.status = "fulfilled";
      state.satatusMsg = "";
    });
  },
});
export default statusSlice;
